<template>
  <div class="star" :class="starType">
    <span v-for="itemClass in itemClasses" :class="itemClass" class="star-item"></span>
  </div>
</template>

<script>
  const length = 5
  const stateOn = 'ON'

  const stateOff = 'Off'

  const stateHalf = 'Half'
  export default {
    name: 'star',
    props: {
      size: {
        type: Number
      },
      score: {
        type: Number
      }
    },
    computed: {
      starType() {
        return 'star-' + this.size
      },
      itemClasses() {
        let result = []
        let score = Math.floor(this.score * 2) / 2
        let dec = score % 1 !== 0
        let interger = Math.floor(score)
        for (let i = 0; i < interger; i++) {
          result.push(stateOn)
        }
        if (dec) {
          result.push(stateHalf)
        }
        while (result.length < length) {
          result.push(stateOff)
        }
        return result
      }
    }
  }
</script>

<style lang="less">
  @import '../common/style/base.less';
 .star {

  .star-item {
    display: inline-block;
  }

 }
 &.star-48{
   .star-item {
     width: 20px;
     height: 20px;
     margin-right: 22px;
     background-size: 100% 100%;
    &.ON{
     .background-img(star48_on);
     }
    &.Half{
     .background-img(star48_half);
     }
    &.Off{
     .background-img(star48_off);
     }
    &:last-child{
       margin-right: 0;
     }
   }


 }

  &.star-36{
  .star-item {
    width: 15px;
    height: 15px;
    margin-right: 6px;
    background-size: 100% 100%;
    &:last-child{
       margin-right: 0;
     }
    &.ON{
     .background-img(star36_on);
     }
    &.half{
     .background-img(star36_half);
     }
    &.off{
     .background-img(star36_off);
     }
  }

  }
</style>



