<template>
  <div class="header-wrapper">
    <div class="header">
      <div class="avatar" @click="showDetail"><img :src="seller.avatar" alt="" width="64" height="64"></div>
      <div class="desc">
        <div class="title">
          <span class="brand"></span>
          <span class="brand-name">{{seller.name}}</span>
        </div>
        <div class="description">
          <span class="desc1" v-show="seller.description">{{seller.description}}/</span>
          {{seller.deliveryTime}}分钟送达
        </div>
        <div class="support" v-if="seller.supports">
          <span class="icon" :class="dataMap[this.seller.supports[0].type]"></span>
          <span class="font">{{seller.supports[0].description}}</span>
        </div>
        <div class="supports-count"  @click="showDetail" v-if="seller.supports">
          {{seller.supports.length}}个
          <span class="arrow">&gt;</span>
        </div>
      </div>
      <div class="background">
        <img width="100%" height="100%" src="http://static.galileo.xiaojukeji.com/static/tms/seller_avatar_256px.jpg">
      </div>
    </div>
    <div class="notice">
      <span class="icon"></span>
      <span class="notice-font">{{seller.bulletin}}</span>
      <span class="notice-arrow">&gt;</span>
    </div>
    <transition name="fade">
      <div class="detail-wrapper" v-show="isShowDetail">
        <h1 class="detail-title">{{seller.name}}</h1>
        <div class="star-box">
          <star :size="48" :score="seller.score"></star>
        </div>
        <div class="discount-info">
          <span class="line"></span>
          <span class="text">优惠信息</span>
          <span class="line"></span>
        </div>
        <ul class="supports" v-if="seller.supports">
          <li class="support-item"  v-for="support in seller.supports">
            <span class="icon" :class="dataMap[support.type]"></span>
            <span class="text">{{support.description}}</span>
          </li>
        </ul>
        <span class="close" @click="hideDetail">&#xe608;</span>
      </div>
    </transition>
    <tabs></tabs>
  </div>
</template>

<script>
  import tabs from './tabs.vue'
  import star from '../star.vue'
  export default {
    name: 'header',
    data() {
      return {
        'isShowDetail': false,
        'dataMap': ['decrease', 'discount', 'guarantee', 'invoice', 'special']
      }
    },
    props: {
      seller: {
        type: Object
      }
    },
    methods: {
      showDetail() {
        this.isShowDetail = true
      },
      hideDetail() {
        this.isShowDetail = false
      }
    },
    components: {tabs, star}
  }
</script>

<style lang="less">
  @import "../../common/style/base.less";

  .header{
    position: relative;
    padding:24px 12px 18px 24px;
    background:rgba(7,17,27,0.5);
    overflow:hidden;
    font-size:0;
    .background {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      -webkit-filter: blur(10px);
      filter: blur(10px);
    }
    .avatar{
      width:64px;
      height:64px;
      display: inline-block;
      vertical-align: top;
    }
    .desc{
      margin-left:16px;
      font-size:16px;
      display: inline-block;
      color:#fff;
      .title{
        margin:0 2px 2px 0;
        .brand{
          display: inline-block;
          vertical-align: top;
          width: 30px;
          height: 18px;
          background-size: 100% 100%;
          .background-img(brand)
        }
      }
      .description{
        font-size: 14px;
        line-height: 20px;
      }
      .support{
        font-size:14px;
        line-height:20px;
        .icon{
          display: inline-block;
          width: 12px;
          height: 12px;
          background-size: 100% 100%;
          &.decrease{
           .background-img(decrease_1)
          }
          &.discount{
           .background-img(discount_1)
          }
          &.guarantee{
           .background-img(guarantee_1)
          }
          &.invoice{
           .background-img(invoice_1)
          }
          &.special{
           .background-img(special_1)
          }
        }
      }
      .supports-count{
        position: absolute;
        right:12px;
        bottom:14px;
        line-height: 24px;
        padding:0 8px;
        border-radius:14px;
        text-align: center;
        font-size: 10px;
        background: rgba(255,255,255,0.2);
      }
    }
  }
  .notice{
    position: relative;
    height: 28px;
    line-height: 28px;
    padding: 0 22px 0 12px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    background: rgba(7,17,27,0.2);
    padding:0 12px;
    .notice-arrow{
      display: inline-block;
      width:12px;
      height:12px;
      position: absolute;
      font-size: 10px;
      right: 6px;
      line-height: 12px;
      top: 8px;
    }
    .icon{
      vertical-align: middle;
      display: inline-block;
      width: 16px;
      height: 12px;
      background-size: 100% 100%;
      .background-img(bulletin)
    }
    .notice-font{
      font-size: 12px;
    }
  }
  .detail-wrapper{
    width:100%;
    height:100%;
    position: fixed;
    text-align: center;
    top:0;
    left:0;
    z-index:100;
    transition:all 0.5s;
    background: rgba(7,17,27,0.8);
    &.fade-enter-active, &.fade-leave-active {
      transition: opacity 0.5s
    }
    &.fade-enter, &.fade-leave-active {
      opacity: 0
    }
    .detail-title{
      color:#fff;
      font-size: 18px;
      font-weight:bold;
      margin:64px auto 16px;
    }
    .discount-info{
      width: 80%;
      margin:0 auto;
      margin-bottom:24px;
      display:flex;
      .line{
        position: relative;
        top:-12px;
        flex:1;
        border-bottom:1px solid #eee;
      }
      .text{
        padding: 6px;
        font-size:16px;
        color:#fff;
      }
    }
    .supports{
      width: 80%;
      margin:28px auto;
      text-align: left;
      .support-item{
        color:#fff;
        font-size: 14px;
        line-height: 20px;
        .icon{
          display: inline-block;
          vertical-align: top;
          display: inline-block;
          width: 16px;
          height: 16px;
          background-size: 100% 100%;
          &.decrease{
           .background-img(decrease_1)
           }
          &.discount{
           .background-img(discount_1)
           }
          &.guarantee{
           .background-img(guarantee_1)
           }
          &.invoice{
           .background-img(invoice_1)
           }
          &.special{
           .background-img(special_1)
           }
        }
      }
    }
    .close{
      display: inline-block;
      position: relative;
      width: 32px;
      height: 32px;
      margin: -64px auto 0 auto;
      clear: both;
      font-size: 32px;
      color:#fff;
    }
  }
</style>



