<template>
  <div class="shopcart">
    <div class="content">
      <div class="content-left" @click="toggleList">
        <div class="icon">
          <span class="circle-big">
            <i class="circle-inner" :class="{'highLight':totalCount>0}">&#xe624;</i>
          </span>
          <span class="products-num" v-show="totalCount>0">{{totalCount}}</span>
        </div>
        <div class="text"><span class="price" :class="{highLight:totalPrice>0}">¥{{totalPrice}}</span><span class="delivery">另需配送费¥{{deliveryPrice}}元</span></div>
      </div>
      <div class="btn" :class="btnColor">{{btnDesc}}</div>
    </div>
    <div class="cart" v-show="listShow">
      <div class="cart-top">
        <span class="title">购物车</span>
        <span class="clear-btn">清空</span>
      </div>
      <div class="cart-content" ref="cartScroll">
        <ul class="content-inner">
          <li class="good" v-for="food in selectFood">
            <span class="food-name">{{food.name}}</span>
            <div class="price">
              <span class="icon">¥</span>
              <span class="price-num">{{food.price*food.count}}</span>
            </div>
            <shopControl :food="food"></shopControl>
          </li>
        </ul>
      </div>
    </div>
    <div class="ball-container">
      <transition name="drop" v-for="ball in balls" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
       <div class="ball" v-show="ball.show">
         <div class="inner inner-hook"></div>
       </div>
      </transition>
    </div>
  </div>
</template>

<script>
	import shopControl from '../shopControl.vue'
	import BScroll from 'better-scroll'
	export default {
    name: 'shopcart',
    data() {
      return {
        data: {},
        balls: [
          {
            show: false
          },
	        {
            show: false
	        },
	        {
            show: false
	        },
	        {
            show: false
	        },
	        {
            show: false
	        }
        ],
        dropBall: [],
        fold: true
      }
    },
    props: {
      selectFood: {
        type: Array,
        default: [{
	        price: 10,
	        num: 1
        }]
      },
      deliveryPrice: {
        type: Number,
        default: 0
      },
      minPrice: {
        type: Number,
        default: 0
      }
    },
    components: {
      shopControl
    },
    methods: {
      drop(el) {
        for (let i = 0; i < this.balls.length; i++) {
          let ball = this.balls[i]
          if (!ball.show) {
            ball.show = true
            ball.el = el
            this.dropBall.push(ball)
            return
          }
        }
      },
      beforeEnter(el) {
        let count = this.balls.length
        while (count--) {
          let ball = this.balls[count]
          if (ball.show) {
            let rect = ball.el.getBoundingClientRect()
            let x = rect.left - 32
            let y = -(window.innerHeight - rect.top - 15)
//            console.log(window.innerHeight, rect.top, y, rect.left)
            el.style.display = ''
            el.style.webkitTransform = `translate3d(0,${y}px,0)`
            el.style.transform = `translate3d(0,${y}px,0)`
            let inner = el.getElementsByClassName('inner-hook')[0]
            inner.style.webkitTransform = `translate3d(${x}px,0,0)`
            inner.style.transfrom = `translate3d(${x}px,0,0)`
          }
        }
	    },
	    enter(el) {
        /* eslint-disable no-unused-vars */
        let rf = el.offsetHeight
        this.$nextTick(() => {
          el.style.webkitTransform = 'translate3d(0,0,0)'
          el.style.transform = 'translate3d(0,0,0)'
          let inner = el.getElementsByClassName('inner-hook')[0]
          inner.style.webkitTransform = 'translate3d(0,0,0)'
          inner.style.transform = 'translate3d(0,0,0)'
        })
	    },
	    afterEnter(el) {
        let ball = this.balls[0]
//        console.log(el)
        if (ball) {
          ball.show = false
//          console.log(el)
	        el.style.display = 'none'
        }
      },
      toggleList() {
        if (!this.totalCount) {
            return
        }
        this.fold = !this.fold
	      this.$root.eventHub.$emit('cart.toggle', this.fold)
//        console.log(this.fold)
      }
    },
    computed: {
      totalPrice() {
        let total = 0
        this.selectFood.forEach((food) => {
          total += food.price * food.count
        })
        return total
      },
      totalCount() {
	      let totalCount = 0
	      this.selectFood.forEach((food) => {
          totalCount += food.count
	      })
	      return totalCount
      },
      btnDesc() {
        if (this.totalPrice === 0) {
          return `¥${this.minPrice}元起送`
        } else if (this.totalPrice < this.minPrice) {
          let diff = this.minPrice - this.totalPrice
          return `还差¥${diff}元起送`
        } else {
          return `去结算`
        }
      },
      btnColor() {
        if (this.totalPrice < this.minPrice) {
          return 'not-enough'
        } else {
          return 'enough'
	      }
      },
	    listShow() {
        if (!this.totalCount) {
          this.fold = true
          return false
        }
        let show = !this.fold
        if (show) {
          this.$nextTick(() => {
            if (!this.cartScroll) {
              this.cartScroll = new BScroll(this.$refs.cartScroll, {
                click: true
              })
            } else {
	            this.cartScroll.refresh()
            }
          })
         }
         return show
	    }
    },
    transitons: {
      drop: {

      }
    }
	}
</script>

<style lang="less">
  @import '../../common/style/base.less';
  .shopcart{
    width:100%;
    position: fixed;
    bottom:0;
    left:0;
    z-index:99;
    height:48px;
    background: #141d27;
    .content {
      display: flex;
      /*padding: 7px 0;*/
      .content-left {
        flex: 1;
      .icon {
        position: relative;
        display: inline-block;
        top: -7px;
        .circle-big {
          display: inline-block;
          width: 44px;
          padding: 6px;
          height: 44px;
          border-radius: 50%;
          background: #141d27;
          .circle-inner {
            display: inline-block;
            width: 44px;
            height: 44px;
            vertical-align: middle;
            line-height: 44px;
            border-radius: 50%;
            background: rgba(255, 255, 255, 0.2);
            color: rgba(255, 255, 255, 0.4);
            font-family: "iconfont";
            font-size: 24px;
            font-style: normal;
            text-align: center;
            &.highLight{
               background: rgb(0,160,220);
               color:#fff;
            }
          }
        }
      }
      .products-num{
        position: absolute;
        top:0;
        right:0;
        border-radius: 15px;
        padding:0 6px;
        color:#fff;
        font-size: 9px;
        line-height: 16px;
        background: red;
        text-align: center;
      }
      .text {
        display: inline-block;
        height: 30px;
        line-height: 30px;
        .price{
          display: inline-block;
          padding-right:15px;
          height:100%;
          color:rgba(255,255,255,0.1);
          font-size:16px;
          font-weight: bold;
          border-right:1px solid rgba(255,255,255,0.1);
          &.highLight{
             color:#fff;
          }
        }
        .delivery{
          margin-left:10px;
        }
      }

      }

    }
    .btn{
      flex:0 0 105px;
      height:30px;
      padding:9px 6px;
      line-height: 30px;
      justify-items: center;
      background:#2b333b;
      align-items: center;
      text-align: center;
      color:rgba(255,255,255,0.4);
      font-size:12px;
      &.not-enough{
         background:#2b333b;
       }
      &.enough{
         background:#00b43c;
         color:#fff;
       }
    }
    .cart{
      position: absolute;
      top:0;
      left:0;
      width:100%;
      z-index:-1;
      -webkit-transition: all 0.5s;
      transition: all 0.5s;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0);
      .cart-top{
        height:40px;
        padding:0 18px;
        line-height: 40px;
        background:#f3f5f7;
        .border-1px(rgba(7,17,27,.1));
        .title{
          font-size:14px;
          font-weight: 200;
          color:rgb(7,17,27);
          line-height: 40px;
        }
        .clear-btn{
          font-size:14px;
          color:rgb(0,160,220);
          float:right;
        }
      }
    }
    .cart-content{
      padding:0 18px;
      background:#fff;
      max-height: 217px;
      overflow:hidden;
      .content-inner{
        color:rgb(7,17,27);
        .price{
          position: absolute;
          right:100px;
          top:12px;
          color:red;
          line-height:24px;
          .icon{
            display: inline-block;
            font-size: 10px;
            line-height:24px;
          }
          .price-num{
            display: inline-block;
            font-size: 14px;
            line-height:24px;
            font-weight:bold;
          }
        }
        .good{
          padding:12px 0;
          height:24px;
          line-height: 24px;
          position: relative;
          .border-1px(rgba(7,17,27,0.1));
        }
        .shop-control{
          position: absolute;
          right:0;
          top:12px;
        }
      }
    }
    .ball-container{
      .ball{
        position: fixed;
        left:32px;
        bottom:22px;
        z-index:200;
        transition: all 0.4s cubic-bezier(1,-0.05,.83,.67);
        .inner{
          width:16px;
          height:16px;
          border-radius: 100%;
          background: rgb(0,160,220);
          transition: all 0.4s cubic-bezier(1,-0.05,.83,.67);
        }
      }
    }
  }
</style>



