<template>
  <transition name="swipe">
  <div class="detail-wrap"  ref="detailWrap"  v-show="isFlag">
    <div class="detail">
      <div class="detail-top">
        <div class="detail-header">
          <img :src="curFood.image" alt="">
          <i class="arrow-left" @click="hideDetail">&lt;</i>
        </div>
        <div class="detail-info">
          <h1 class="info-title">{{curFood.name}}</h1>
          <div class="info-desc">
            <span class="soldNum">月售{{curFood.sellCount}}件</span>
            <span class="rating">好评率{{curFood.rating}}%</span>
          </div>
          <div class="price">
            <span class="now">¥{{curFood.price}}</span>
            <span class="old" v-show="curFood.oldPrice">¥{{curFood.oldPrice}}</span>
          </div>
          <shopControl class="cartControl" :food="curFood"></shopControl>
          <div class="addCart" v-show="!curFood.count || curFood.count === 0" @click="addFirst($event)">加入购物车</div>
        </div>
      </div>
      <div class="info">
        <h1 class="title">商品信息</h1>
        <p class="text">{{curFood.info}}</p>
      </div>
      <div class="rating">
        <h1 class="title">商品评价</h1>
        <div class="ratingselect">
          <div class="rating-type border-1px">
            <span class="block positive" @click="chooseType('all')" :class="{'active':selectType === 'all'}">全部<span class="count">3</span></span>
            <span class="block positive" @click="chooseType(0)" :class="{'active':selectType === 0}">推荐<span class="count">2</span></span>
            <span class="block negative" @click="chooseType(1)" :class="{'active':selectType === 1}">吐槽<span class="count">1</span></span>
          </div>
          <div class="switch" :class="{'on':onlyContent}">
            <span class="icon-check_circle" @click="onlyContentToggle">&#xe648;</span>
            <span class="text">只看有内容的评价</span>
          </div>
          <div class="rating-wrapper">
            <ul class="rating-list" v-show="curFood.ratings && curFood.ratings.length">
              <li class="each-rating" v-for="rating in curFood.ratings" v-show="needShow(rating.rateType,rating.text)">
                <div class="user">{{rating.username}}</div>
                <div class="time">{{rating.rateTime | time}}</div>
                <div class="text">
                  <span class="icon like" v-show="rating.rateType === 0">&#xe625;</span>
                  <span class="icon unlike" v-show="rating.rateType === 1">&#xe83f;</span>
                  {{rating.text}}
                </div>
              </li>
            </ul>
            <div class="rating-none" v-show="!curFood.ratings || !curFood.ratings.length">暂无评价</div>
          </div>
        </div>
      </div>
    </div>
  </div>
  </transition>
</template>

<script>
  import shopControl from '../shopControl.vue'
  import BScroll from 'better-scroll'
  import Vue from 'vue'
  export default {
    name: 'detail',
    props: {
      curFood: {
        type: Object,
        default: {}
      }
    },
    data() {
      return {
        isFlag: false,
        onlyContent: false,
        selectType: 'all'
      }
    },
    filters: {
      time(timeStamp) {
        let date = new Date(timeStamp)
        let Y = date.getFullYear()
        let m = date.getMonth() + 1
        let d = date.getDate()
        let H = date.getHours()
        let i = date.getMinutes()
        let s = date.getSeconds()
        if (m < 10) {
          m = '0' + m
        }
        if (d < 10) {
          d = '0' + d
        }
        if (H < 10) {
          H = '0' + H
        }
        if (i < 10) {
          i = '0' + i
        }
        if (s < 10) {
          s = '0' + s
        }
        let t = Y + '-' + m + '-' + d + ' ' + H + ':' + i + ':' + s
        return t
      }
    },
    methods: {
      showDetail() {
        this.isFlag = true
        console.log(this.$refs.detailWrap)
        this.$nextTick(() => {
          if (!this.cartScroll) {
            this.cartScroll = new BScroll(this.$refs.detailWrap, {
              click: true
            })
          } else {
            this.cartScroll.refresh()
          }
        })
      },
      hideDetail() {
        this.isFlag = false
      },
      addFirst(event) {
        console.log(event)
        if (!event._constructed) {
          return
        }
        this.$root.eventHub.$emit('cart.add', event.target)
        Vue.set(this.curFood, 'count', 1)
        console.log(this.curFood)
      },
      chooseType(index) {
        this.selectType = index
        this.$nextTick(() => {
          if (!this.cartScroll) {
            this.cartScroll = new BScroll(this.$refs.detailWrap, {
              click: true
            })
          } else {
            this.cartScroll.refresh()
          }
        })
      },
      onlyContentToggle() {
        this.onlyContent = !this.onlyContent
        this.$nextTick(() => {
          if (!this.cartScroll) {
            this.cartScroll = new BScroll(this.$refs.detailWrap, {
              click: true
            })
          } else {
            this.cartScroll.refresh()
          }
        })
      },
      needShow(ratingType, text) {
        if (this.onlyContent && !text) {
          return false
        }
        if (this.selectType === 'all') {
          return true
        } else {
          return this.selectType === ratingType
        }
      }
    },
    components: {
      shopControl
    }
  }
</script>

<style lang="less">
  @import '../../common/style/base.less';
  .detail-wrap{
    background: rgba(255,255,255,1);
    width:100%;
    position:fixed;
    top:0;
    bottom:48px;
    left:0;
    z-index:97;
    transition: all .3s linear;
    transform:translate3d(0,0,0);
  }
  .detail-top{
    .border-1px(rgba(7,17,27,0.1))
  }
  .detail{
    transition: all .3s linear;
    transform:translate3d(0,0,0);
    .detail-header{
      position: relative;
      width:100%;
      height:0;
      padding-top:100%;
      img{
        position: absolute;
        top:0;
        left:0;
        width:100%;
        height:100%;
      }
      .arrow-left{
        position: absolute;
        top:0;
        left:0;
        color:#fff;
        padding:10px;
        font-weight: bold;
      }
    }
    .detail-info{
      position: relative;
      padding:18px;
      .info-title{
        line-height: 14px;
        margin-bottom: 8px;
        font-size: 14px;
        font-weight: 700;
        color: #07111b;
      }
      .info-desc{
        margin-bottom: 18px;
        line-height: 10px;
        height: 10px;
        font-size: 0;
        span{
          font-size: 10px;
          color: #93999f;
        }
        .rating{
          margin-left:15px;
        }
      }
      .price{
        font-size: 14px;
        .now{
          color:red;
          margin-right:10px;
        }
      }
      .cartControl{
        position: absolute;
        right:12px;
        bottom:12px;
      }
      .addCart{
        position: absolute;
        right:12px;
        bottom:12px;
        border-radius: 12px;
        padding:0 12px;
        height:25px;
        line-height: 25px;
        font-size:12px;
        background:#00a0dc;
        color:#fff
      }
    }
  }
  .swipe-enter,.swipe-leave{
    transform:translate3d(100%,0,0);
  }
  .swipe-leave-active{
    transform:translate3d(100%,0,0);
  }
  .info{
    margin-top:18px;
    border-top:1px solid rgba(7,17,27,0.1);
    border-bottom:1px solid rgba(7,17,27,0.1);
    padding:18px;
    .title{
      line-height: 14px;
      margin-bottom: 6px;
      font-size: 14px;
      color: #07111b;
    }
    .text{
      line-height: 24px;
      padding: 0 8px;
      font-size: 12px;
      color: #4d555d;
    }
  }
  .rating{
    .title{
      line-height: 14px;
      margin-left: 18px;
      font-size: 14px;
      color: #07111b;
    }
    .rating-type {
      padding: 18px 0;
      margin: 0 18px;
      position: relative;
      font-size: 0;
      .border-1px(rgba(7,17,27,0.1));
      .block {
        display: inline-block;
        padding: 8px 12px;
        margin-right: 8px;
        line-height: 16px;
        border-radius: 1px;
        font-size: 12px;
        color: #4d555d;
        &.positive {
          background: rgba(0,160,220,0.2);
          &.active {
            background: #00a0dc;
            color:#fff;
          }
        }
        &.negative {
          background: rgba(77,85,93,0.2);
          &.active {
            background: #4d555d;
            color:#fff;
          }
        }
      }
    }
    .switch {
      padding: 12px 18px;
      line-height: 24px;
      border-bottom: 1px solid rgba(7,17,27,0.1);
      color: #93999f;
      font-size: 0;
      .icon-check_circle{
        font-family:"iconfont";
        font-size:24px;
        font-style:normal;
        color:#eee;
      }
      &.on{
         .icon-check_circle {
          color: #00c850;
          }
       }
      .text{
        display: inline-block;
        vertical-align: top;
        font-size: 12px;
      }
    }
    .rating-wrapper{
      padding: 0 18px;
      .each-rating{
        padding: 16px 0;
        position: relative;
        .user{
          position: absolute;
          right: 0;
          top: 16px;
          line-height: 12px;
          font-size: 10px;
          color: #93999f
        }
        .time{
          margin-bottom: 6px;
          line-height: 12px;
          font-size: 10px;
          color: #93999f;
        }
        .text{
          font-size: 12px;
          .icon{
            font-family:"iconfont";
            color: #93999f;
            font-size: 12px;
            &.like{
              color:#00a0dc
             }
          }
        }
      }
    }
  }
</style>



