<template>
  <div class="shop-control">
    <transition name="move">
      <div class="increase-outer" v-show="food.count>0" @click.stop.prevent="increase($event)" :class="{'transition':food.count>0}">
        <span class="increase" v-if="">&#xe644;</span>
      </div>
    </transition>
    <transition name="fade" >
      <span class="num" v-show="food.count>0" :class="{'transition':food.count>0}">{{food.count}}</span>
    </transition>
    <span class="decrease"  @click.stop.prevent="add($event)">&#xe62d;</span>
  </div>
</template>

<script>
  import Vue from 'vue'

  export default {
    name: 'shopControl',
    props: {
      food: {
        type: Object,
        default: {}
      }
    },
    methods: {
      add(event) {
        if (!event._constructed) {
          return
        }
        if (!this.food.count) {
          Vue.set(this.food, 'count', 1)
        } else {
          this.food.count ++
//          console.log(this.food.count, 'count')
        }
        this.$root.eventHub.$emit('cart.add', event.target)
      },
      increase(event) {
        if (!event._constructed) {
          return
        }
        this.food.count--
//        console.log(this.food.count, 'descrease')
      }
    }
  }
</script>

<style lang="less">
  .shop-control{
    float:right;
    margin-right:18px;
    .increase-outer{
      display: inline-block;
      transition:all .4s linear;
      transform:translate3D(0,0,0);
      &.move-enter,&.move-leave-active{
        opacity:0;
        transform:translate3D(24px,0,0);
        .increase{
          transition:all .4s linear;
          transform:rotate(180deg);
        }
      }
      /*&.move-enter-active,&.move-leave{*/
        /*opacity:1;*/
        /*transform:translate3D(0,0,0);*/
        /*.increase{*/
          /*&.move-enter-ative,&.move-leave{*/
          /*transform:rotate(0deg);*/
          /*}*/
        /*}*/
      /*}*/
      .increase{
        display: inline-block;
        transition:all 0.4s linear;
        transform:rotate(0deg);
      }
    }
    .num{
      width:24px;
      transition:all 0.4s;
      &.fade-enter,&.fade-leave-active{
        opacity:0;

      }
      &.fade-leave,&.fade-enter-active{
        opacity:1;
      }
    }
  }
</style>



