import Vue from 'vue'
import Router from 'vue-router'
import goods from '@/components/goods/goods'

Vue.use(Router)

export default new Router({
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      name: 'Goods',
      component: goods
    },
    {
      path: '/goods',
      name: 'Goods',
      component: goods
    }
  ]
})



