<template>
  <div class="goods-wrapper">
    <div class="wrapper" ref="menuWrapper">
      <ul class="goods-type">
        <li class="type-item type-hook" v-for="(item,index) in goods" :class="{'active':curIndex === index}" @click="scrollTo(index, $event)">
          <span class="icon" v-show="item.type > 1"></span>
          <span class="text">{{item.name}}</span>
        </li>
      </ul>
    </div>
    <div class="goods-content" ref="foodWrapper">
      <ul>
        <div class="good food-list-hook" v-for="item in goods">
        <h1 class="good-title">{{item.name}}</h1>
        <li class="good-item" v-for="good in item.foods" @click="detailFood(good,$event)">
          <div class="item-inner">
            <span class="good-img"><img :src="good.image" alt="" width="64" height="64"></span>
            <div class="good-text">
              <p class="name">{{good.name}}</p>
              <p class="description">{{good.description}}</p>
              <p class="sellCount"><span class="count">月售{{good.sellCount}}件</span><span class="rating">好评率{{good.rating}}%</span></p>
              <div class="price">
                <span class="nowPrice"><i class="icon">¥</i>{{good.price}}</span>
                <del class="oldPrice" v-if="good.oldPrice">¥{{good.oldPrice}}</del>
                <shopControl :food="good"></shopControl>
              </div>
            </div>
          </div>
        </li>
      </div>
      </ul>
    </div>
    <shopcart ref="shopcart" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice" :select-food="selectFood"></shopcart>
    <div class="list-mask" v-show="!this.fold"></div>
    <detail :curFood="selectedFood" ref="detail"></detail>
  </div>
</template>

<script>
import BScroll from 'better-scroll'
import shopcart from '../shopcart/shopcart.vue'
import shopControl from '../shopControl.vue'
import detail from './detail.vue'
export default {
  name: 'goods',
  props: {
    seller: {
      type: Object
    }
  },
  data() {
    return {
      goods: [],
      listHeight: [],
      scrollY: 0,
      fold: true,
      selectedFood: {}
    }
  },
  computed: {
    curIndex() {
      for (let i = 0; i < this.listHeight.length; i++) {
        let height1 = this.listHeight[i]
        let height2 = this.listHeight[i + 1]
        if (!height2 || this.scrollY >= height1 && this.scrollY < height2) {
//          console.log(this.scrollY, height1, height2)
          return i
        }
      }
      return 0
    },
    selectFood() {
      let foods = []
      this.goods.forEach((good) => {
        good.foods.forEach((food) => {
          if (food.count > 0) {
            foods.push(food)
          }
        })
      })
//      console.log(foods)
      if (foods.length < 1) {
        this.fold = true
      }
      return foods
    }
  },
  created() {
    this.$http.get('/api/goods').then((res) => {
      res = res.body
      if (res.error === 0) {
        this.goods = res.data
        this.$nextTick(() => {
          this.initScroll()
          this.calHeight()
        })
      }
    })
//    console.log(Vue)
    this.$root.eventHub.$on('cart.add', this.drop)
    this.$root.eventHub.$on('cart.toggle', this.toggle)
    this.$root.eventHub.$on('cart.clear', this.clear)
  },
  methods: {
    detailFood(food, event) {
      if (!event._constructed) {
        return
      }
      this.selectedFood = food
      console.log(food, 'food')
      this.$refs.detail.showDetail()
    },
    initScroll() {
      this.menuScroll = new BScroll(this.$refs.menuWrapper, {
        click: true
      })
      this.foodScroll = new BScroll(this.$refs.foodWrapper, {
        probeType: 3,
        click: true
      })
      this.foodScroll.on('scroll', (pos) => {
        this.scrollY = Math.abs(Math.round(pos.y))
        console.log(this.scrollY)
      })
    },
    calHeight() {
      let height = 0
      let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
      this.listHeight.push(height)
      for (let i = 0; i < foodList.length; i++) {
        let item = foodList[i]
        height += item.clientHeight
        this.listHeight.push(height)
      }
//      console.log(this.listHeight)
    },
    scrollTo(index, event) {
      if (!event._constructed) {
        return
      }
      let foodList = this.$refs.foodWrapper.getElementsByClassName('food-list-hook')
      let el = foodList[index]
      this.foodScroll.scrollToElement(el, 300)
    },
    drop(target) {
//        console.log(target)
        this.$refs.shopcart.drop(target)
    },
    toggle(val) {
      this.fold = val
    },
    clear(val) {
      this.selectFood = val
    }
  },
  components: {
    shopcart,
    shopControl,
    detail
  }
}
</script>

<style lang="less">
  @import '../../common/style/base.less';

  .goods-wrapper{
    display: flex;
    position: absolute;
    top: 174px;
    bottom: 46px;
    width: 100%;
    overflow: hidden;
    .goods-type{
      flex:0 0 80px;
      width: 80px;
      background: #f3f5f7;
      .type-item{
        padding: 0 12px;
        display:table;
        width:56px;
        height:54px;
        line-height:14px;
        .border-1px(#eee);
        &.active{
          background: #999;
        }
        .text{
          display: table-cell;
          vertical-align: middle;
          color:#000;
          font-size: 12px;
        }
      }
    }
    .goods-content{
      flex:1;
      /*overflow-y:scroll;*/
      .good{
        .good-title{
          border-left:2px solid #d9dde1;
          padding-left: 14px;
          font-size:12px;
          height:26px;
          line-height: 26px;
          color:rgb(147,153,159);
          background: #f3f5f7;
        }
        .good-item{
          padding:0 18px;
          .item-inner{
            .border-1px(#ccc);
            padding:20px 0;
            font-size:0;
            display:flex;
            .good-img{
              margin-right:4px;
              img{
                vertical-align: top;
              }
            }
            .good-text{
              display: inline-block;
              font-size: 12px;
              color:rgb(7,17,27);
              flex:1;
              p{
                width:100%;
              }
              .name{
                line-height: 14px;
                margin-top:4px;
              }
              .description{
                margin:8px 0;
                color:rgb(147,153,159);
                font-size: 10px;
                line-height: 10px;
              }
              .sellCount{
                color:rgb(147,153,159);
                font-size: 10px;
                line-height: 10px;
                 margin-bottom:8px;
                .count{
                  margin-right:8px;
                }
              }
              .price{
                .nowPrice{
                  font-size: 20px／28px;
                  color:red;
                  font-weight:bold;
                  margin-right:8px;
                  .icon{
                    font-size: 28px／28px;
                    color:red;
                  }
                }
              }
            }

          }
          &:last-child{
            border:none;
          }
        }
      }
    }
    .list-mask {
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: 98;
      background: rgba(7,17,27,0.6);
    }
    .good-detail{
      width:100%;
      height:100%;
      background: rgba(0,0,0,0.3);
    }
  }
</style>



